* {
  box-sizing: border-box;
  margin: 0;
}

#root {
  height: 100%;
  background-color: #fdfdfd;
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

hr {
  width: 100%;
  border: 0;
  border-top: 1px solid #ddd;
}

.page-header {
  padding: 3%;
  display: flex;
}

.page-header-left, .page-header-center, .page-header-right {
  align-items: center;
  display: flex;
}

.page-header-left {
  flex: 1;
  justify-content: flex-start;
}

.page-header-center {
  flex: 2;
  justify-content: center;
}

.page-header-right {
  flex: 1;
  justify-content: flex-end;
}

.page-title {
  color: #0f0f0f;
  text-transform: uppercase;
  font-family: Palatino, Times New Roman, serif;
  font-size: 45px;
  line-height: .8;
}

.instagram-icon {
  vertical-align: middle;
}

.page-content {
  max-width: 1400px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: 0 auto;
  padding: 0 70px;
  display: flex;
}

.page-footer {
  text-transform: uppercase;
  justify-content: center;
  padding: 3%;
  font-family: Baskerville, Garamond, Times New Roman, serif;
  font-size: 14px;
  display: flex;
}

@media (max-width: 860px) {
  #root {
    justify-content: space-around;
  }

  .page-header {
    padding: 22px 0;
  }

  .page-header-left, .page-header-right {
    display: none;
  }

  .page-title {
    font-size: 32px;
  }

  .page-content {
    padding: 0 10px;
  }
}

.patternList {
  flex-direction: column;
  justify-content: space-evenly;
  gap: 30px;
  padding: 10px;
  display: flex;
}

.pattern {
  justify-content: space-evenly;
  gap: 25px;
  display: flex;
}

.pattern:nth-child(2n) {
  flex-direction: row-reverse;
}

.pattern-image {
  height: 100%;
  max-height: 400px;
  width: 100%;
  max-width: 400px;
  border: 1px solid #888;
  border-radius: 5px;
  flex: 1;
}

.pattern-info {
  flex-direction: column;
  flex: 1;
  justify-content: center;
  align-items: flex-start;
  gap: 25px;
  display: flex;
}

.pattern-info h1 {
  color: #0f0f0f;
  text-transform: uppercase;
  font-family: Palatino, Times New Roman, serif;
  font-size: 35px;
  font-weight: 300;
}

.pattern-info p {
  color: #606060;
  font-family: Baskerville, Garamond, Times New Roman, serif;
  font-size: 20px;
}

.purchase-buttons {
  width: 100%;
  justify-content: flex-start;
  align-items: center;
  gap: 10px;
  display: flex;
}

.purchase-button {
  width: fit-content;
  color: #000;
  text-transform: uppercase;
  border-radius: 10px;
  padding: 10px 16px;
  font-family: Baskerville, Garamond, Times New Roman, serif;
  font-size: 14px;
  text-decoration: none;
}

.purchase-button-strikkeland {
  background-color: #fafafa;
  border: 1.5px solid #3c5fd7;
}

.purchase-button-strikkeland:hover {
  color: #fff;
  background-color: #3c5fd7;
}

.purchase-button-ravelry {
  background-color: #fdfdfd;
  border: 1.5px solid #e67864;
}

.purchase-button-ravelry:hover {
  color: #fff;
  background-color: #e67864;
}

@media (max-width: 1200px) {
  .pattern-image {
    max-height: 350px;
    max-width: 350px;
  }
}

@media (max-width: 860px) {
  .pattern, .pattern:nth-child(2n) {
    flex-direction: column;
    align-items: center;
  }

  .pattern-info {
    align-items: center;
  }

  .pattern-info h1 {
    font-size: 26px;
  }

  .pattern-info p {
    font-size: 18px;
  }

  .purchase-buttons {
    justify-content: center;
  }

  .purchase-button {
    padding: 6px 12px;
    font-size: 12px;
  }
}

